@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  @apply bg-black;
  @apply text-white;
  @apply fira font-sans;
}
.fira {
  font-family: 'Fira Sans Condensed', sans-serif;
}
.fred {
  font-family: 'Fredoka One', cursive;
}
.modal-anim {
  animation-duration:.3s;
  animation-name:slide-in;
}
@keyframes slide-in {
  from {
     top:-50%;
  }
  
  to {
     top:0;
  }

}
.web3modal-modal-lightbox.web3modal-modal-lightbox { z-index: 999; }